import {
    INCREMENT,
    DECREMENT,
    DICTIONARY,
    DIFFICULTY, TYPE, AMOUNT
} from "../actions/actionTypes";

const initialState = {
    count: 0,
    type_dictionary: "",
    type_difficulty: "",
    type: "",
    amount: ""
};

const reducer = (state = initialState, action) => {
    switch(action.type){
        case INCREMENT:
            return {
                ...state,
                count: state.count + action.payload
            }
        case DECREMENT:
            return {
                ...state,
                count: state.count - action.payload
            }
        case DICTIONARY:
            return {
                ...state,
                type_dictionary: action.payload
            }
        case DIFFICULTY:
            return {
                ...state,
                type_difficulty: action.payload
            }
        case TYPE:
            return {
                ...state,
                type: action.payload
            }
        case AMOUNT:
            return {
                ...state,
                amount: action.payload
            }
        default:
            return state
    }
}

export default reducer;

