import React, {useEffect, useState} from "react";
import {Box, CircularProgress, TextField} from "@mui/material";
import {InputField} from "./Input/InputField";
import {useAxios} from "../../hooks/useAxios";
import {useDispatch} from "react-redux";
import {handleAmountChange} from "../../redux/actions/actions";

export const Form = () => {
    const baseUrl = "https://opentdb.com/api_category.php"
    const {response, error, loading} = useAxios(baseUrl)

    const dispatch = useDispatch();

    let inputFieldsInfo = [
        {id: 1, options: response?.trivia_categories, label: "Dictionary"},
        {
            id: 2,
            options: [
                {name: "Easy"},
                {name: "Middle"},
                {name: "Hard"}
            ],
            label: "Difficulty"
        },
        {
            id: 3,
            options: [
                {name: "Multiple"},
                {name: "Boolean"},
            ],
            label: "Type"
        }
    ]

    const getNumberOfQuestions = (event) => {
        dispatch(handleAmountChange(event.target.value))
    }

    console.log(response)
    console.log(inputFieldsInfo)

    return(
        <>
            {loading ?
                <Box>
                    <CircularProgress />
                </Box>
                :
                <Box sx={{ minWidth: 120, mt: 3}}>
                    <form action="">
                        {
                            inputFieldsInfo.map((inputField) => (
                                <Box sx={{mt: 2}} key={inputField.id}>
                                    <InputField options={inputField.options} label={inputField.label} loading={loading}/>
                                </Box>
                            ))
                        }
                        <Box sx={{mt: 2}}>
                            <TextField
                                fullWidth
                                id="filled-number"
                                label="Number"
                                type="number"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={getNumberOfQuestions}
                            />
                        </Box>
                    </form>
                </Box>
            }
        </>
    )
}
