import React, {useEffect, useState} from "react";
import {Box, CircularProgress} from "@mui/material";
import {useAxios} from "../../hooks/useAxios";
import {useSelector} from "react-redux";

import QuestionDetails from "../QuestionDetails/QuestionDetails";

export const Questions = () => {
    const typeDictionary = useSelector(state => state.type_dictionary);
    const typeDifficulty = useSelector(state => state.type_difficulty);
    const type = useSelector(state => state.type);
    const amount = useSelector(state => state.amount)

    console.log(typeDictionary, typeDifficulty.toLowerCase(), type, amount)

    let appUrl = `https://opentdb.com/api.php?amount=${amount}&category=15&difficulty=${typeDifficulty.toLowerCase()}&type=${type.toLowerCase()}`
    const {response, error, loading} = useAxios(appUrl);


    console.log(response)

    return (
        <>
            {loading ?
                <Box>
                    <CircularProgress/>
                </Box>
                :
                <Box>
                    {
                        response.results.map((result) => (
                            <QuestionDetails result={result} key={result.question}/>
                        ))
                    }
                </Box>
            }
        </>
    )
}
