import React from "react"
import Typography from "@mui/material/Typography";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import {Box} from "@mui/material";

const QuestionDetails = ({result}) => {
    return (
        <Box>
            <Typography variant="h4" mt={5}>Question {result.question}</Typography>
            <FormControl>
                <FormLabel id="demo-radio-buttons-group-label">Gender</FormLabel>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    defaultValue="female"
                    name="radio-buttons-group"
                >
                    {
                        result.incorrect_answers.map((incorrect_answer) => (
                            <FormControlLabel value={incorrect_answer} control={<Radio />} label={incorrect_answer} />
                        ))
                    }
                    {/*<FormControlLabel value="male" control={<Radio />} label="Male" />*/}
                    {/*<FormControlLabel value="other" control={<Radio />} label="Other" />*/}
                </RadioGroup>
            </FormControl>
        </Box>
    )
}


export default QuestionDetails
