// core
import React from "react";
import {useDispatch, useSelector} from "react-redux";

// library ---------------- material-ui ---------------
import {Box, Button, Stack} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";

// hooks
import {useNavigate}  from "react-router-dom";

// internal  --------------- redux --------------------
import {
    handleIncrementChange,
    handleDecrementChange
} from "../../redux/actions/actions";

// components
import {Form} from "../forms/Form";

export const Main = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const counter  = useSelector(state => state.count);
    const typeDictionary = useSelector(state => state.type_dictionary);
    const typeDifficulty = useSelector(state => state.type_difficulty);
    const type = useSelector(state => state.type);
    const amount = useSelector(state => state.amount)

    const addIncrement = () => {
        dispatch(handleIncrementChange(1))
    }

    const addDecrement = () => {
        dispatch(handleDecrementChange(1))
    }

    const showData = () => {
        console.log(typeDictionary, typeDifficulty, type, amount)
        navigate('/questions');
    }

    return(
        <>
            <main>
                <div>
                    <Stack direction="column" spacing={1.5} sx={{ mt: 2}}>
                        <Button fullWidth variant="contained" startIcon={<DeleteIcon />} onClick={() => addIncrement()}>Increment</Button>
                        <Button fullWidth variant="outlined" onClick={() => addDecrement()}>Decrement</Button>
                    </Stack>
                    <Box>
                        {counter}
                    </Box>
                    <Form />
                    <Box sx={{mt: 8}}>
                        <Button fullWidth variant="contained" startIcon={<DeleteIcon />} onClick={() => showData()}>Submit
                        </Button>
                    </Box>

                    <Box>
                        {typeDictionary ? typeDictionary : "there is no selected option here"}
                    </Box>
                    <Box>
                        {typeDifficulty ? typeDifficulty : "there is no selected option here"}
                    </Box>
                    <Box>
                        {type ? type : "there is no selected option here"}
                    </Box>
                </div>
            </main>
        </>
    )
}
