import React, {useState} from "react";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import {useDispatch} from "react-redux";

import {
    DICTIONARY,
    DIFFICULTY,
    TYPE
} from "../../../redux/actions/actionTypes";

import {
    handleDictionaryChange,
    handleDifficultyChange,
    handleTypeChange
} from "../../../redux/actions/actions";


export const InputField = ({options, label}) => {
    const [selectOption, setSelectOption] = useState('');
    const dispatch = useDispatch();

    const handleChange = (event) => {
        setSelectOption(event.target.value);

        switch(label.toUpperCase()){
            case DICTIONARY:
                dispatch(handleDictionaryChange(event.target.value));
                break;
            case DIFFICULTY:
                dispatch(handleDifficultyChange(event.target.value));
                break;
            case TYPE:
                dispatch(handleTypeChange(event.target.value));
                break;
        }
    };

    return(
        <>
            <FormControl fullWidth>
                <InputLabel>{label}</InputLabel>
                <Select
                    id="demo-simple-select"
                    value={selectOption}
                    label={label}
                    onChange={handleChange}
                >
                    {
                        options.map(option => (
                            <MenuItem key={option.name} value={option.name}>{option.name}</MenuItem>
                            )
                        )
                    }
                </Select>
            </FormControl>
        </>
    )
}

