import {
    INCREMENT,
    DECREMENT,
    DICTIONARY,
    DIFFICULTY,
    TYPE,
    AMOUNT
} from "./actionTypes";

export const handleIncrementChange = (payload) => {
    return{
        type: INCREMENT,
        payload
    }
}

export const handleDecrementChange = (payload) => {
    return{
        type: DECREMENT,
        payload
    }
}

export const handleDictionaryChange = (payload) => {
    return{
        type: DICTIONARY,
        payload
    }
}

export const handleDifficultyChange = (payload) => {
    return{
        type: DIFFICULTY,
        payload
    }
}

export const handleTypeChange = (payload) => {
    return{
        type: TYPE,
        payload
    }
}

export const handleAmountChange = (payload) => {
    return{
        type: AMOUNT,
        payload
    }
}

